@tailwind base; 
@tailwind components; 
@tailwind utilities;

body {
  margin: 0 auto;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  background:linear-gradient(to right, rgb(187, 247, 208), rgb(134, 239, 172), rgb(59, 130, 246));
  
}

header {
	width:50%;
	text-align:center;
	margin: 0 auto;
}

form {
	width:50%;
	text-align:center;
	display: inline-block;
	margin: 20 auto;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.todo {
  cursor: pointer;
  text-transform: uppercase;
  font-weight: 500;
  border-width: 1px;
     padding:3px;
   width:36%;
   margin: auto;
   margin-bottom:13px;
    border-radius: 3px;
	border-color:lightgray;
	color:black;
	background-color:#ececec;
}
.strike {
  text-decoration: line-through;
  color:red;
  text-transform: uppercase;
  font-weight: 500;
   padding:3px;
   width:36%;
   margin: auto;
   border-width: 1px;
   margin-bottom:13px;
   border-radius: 3px;
   border-color:red;
   background-color:pink;
}
